import React from 'react'
import { Row, Col, message, Form, Spin } from 'antd'
import { PlusOutlined, MinusOutlined } from '@ant-design/icons'
import { replace, map } from 'ramda'
import { RouteComponentProps } from 'react-router-dom'
import useRouteLeaveConfirm from '@/hooks/useRouteLeaveConfirm'
import useTitle from '@/hooks/useTitle'
import useConfirm, { modal } from '@/hooks/useConfirm'
import { useBreadcrumb, useHeaderContent } from '@/hooks/useApp'
import { Page } from '@/components/layout'
import Button from '@/components/button'
import Input from '@/components/input'
import ReactDndProvider from '@/components/snippet-scrollable-container'
import SnippetListMenu from '@/components/snippet-list-menu'
import SnippetDragLayer from '@/components/snippet-drag-layer'
import SnippetsContainer from '@/components/snippets-container'
import Collapse from '@/components/collapse'
import { usePhraseItem, useCreateOrUpdatePhrase } from '@/services/phrase'
import { useReportPreview, useExportTemplate } from '@/services/report-template'
import history from '@/utils/history'
import { rules } from '@/utils/form'
import { PHRASE_EDIT, PHRASE_LIST } from '@/constants/routes'
import { reportTemplateStatusEnums } from '@/constants/enums'
const { compose } = require('ramda')

interface Props extends RouteComponentProps<{ id: string }> {}

export default function PhraseEdit(props: Props) {
  useTitle('結構式報告模版')

  useBreadcrumb([{ name: '報告模版' }, { name: '片語模版' }])

  const { params } = props.match

  const [isPreview, handlePreview] = useReportPreview()

  const {
    loading,
    phraseTemplate,
    snippetList,
    handleSnippetUpdate,
  } = usePhraseItem({
    id: params.id,
  })

  console.log(snippetList)
  console.log(phraseTemplate)

  // reset form field values while report data changed
  React.useEffect(() => {
    if (phraseTemplate) {
      form.setFieldsValue(phraseTemplate)
    }
  }, [JSON.stringify(phraseTemplate)]) // eslint-disable-line

  const [form] = Form.useForm()

  const [creating, handleAdd] = useCreateOrUpdatePhrase({
    onCompleted: response => {
      history.push(replace(':id', response?.data.name)(PHRASE_EDIT))
      message.success('片語模版已建立成功')
    },
  })

  const [updating, handleUpdate] = useCreateOrUpdatePhrase({
    onCompleted: response => {
      message.success('片語模版已儲存成功')
      history.push(replace(':id', response?.data.name)(PHRASE_EDIT))

      if (modal) {
        modal.destroy()
      }
    },
    onError: () => {
      if (modal) {
        handlePublicFailure()
      }
    },
  })

  const handleSave = () => {
    form.validateFields().then(values => {
      let handler = phraseTemplate?.id ? handleUpdate : handleAdd

      handler({
        ...phraseTemplate,
        ...values,
        setting: compose(
          map(({ Component, icon, ...x }) => ({
            ...x,
          }))
        )(snippetList),
      })
    })
  }

  const [handlePublic, handlePublicFailure] = useConfirm({
    content: `發佈後片語模版將可以在報告中使用，確認要發佈嗎？`,
    onOk: () =>
      handleUpdate({
        id: phraseTemplate.id,
        status: reportTemplateStatusEnums.FINISH,
      }),
  })

  const handleConfirm = useRouteLeaveConfirm(PHRASE_LIST)

  const handleExport = useExportTemplate({
    filename: '片語模版',
    data: { ...phraseTemplate, setting: snippetList as any },
  })

  useHeaderContent(
    <div className="inline-flex">
      <Button
        color="outline"
        className="ml-6 bg-transparent text-white border-white"
        onClick={handlePublic}>
        發佈
      </Button>
      <Button
        color="outline"
        className="ml-6 bg-transparent text-white border-white"
        onClick={handleSave}>
        儲存
      </Button>
      <Button
        color="outline"
        className="ml-6 bg-transparent text-white border-white"
        onClick={handleConfirm}>
        取消
      </Button>
      <Button
        color="outline"
        className="ml-6 bg-transparent text-white border-white"
        onClick={handleExport}>
        匯出
      </Button>
      <Button
        color="outline"
        className="ml-6 bg-transparent text-white border-white"
        onClick={handlePreview}>
        預覽
      </Button>
    </div>
  )

  return (
    <ReactDndProvider>
      <SnippetDragLayer />
      <Spin spinning={loading || creating || updating}>
        <Page className="pr-12">
          <Form form={form} initialValues={{ ...phraseTemplate }}>
            <Row gutter={48} className="pt-2 relative">
              <Col span={5}>
                <Collapse
                  defaultActiveKey={[1, 2]}
                  expandIconPosition="right"
                  className="shadow-2xl header-pl-clear"
                  expandIcon={({ isActive }) =>
                    isActive ? <MinusOutlined /> : <PlusOutlined />
                  }>
                  <Collapse.Panel header="片語模版" key={1}>
                    <Row className="mb-2 text-sm text-darkGrey">
                      <Col span={24} className="mb-3">
                        <div className="inline-block" css={{ width: 60 }}>
                          名稱:
                        </div>
                        <Form.Item noStyle name="name" rules={[rules.required]}>
                          <Input className="w-2/3" />
                        </Form.Item>
                      </Col>
                      <Col span={24} className="my-3">
                        <div className="inline-block" css={{ width: 60 }}>
                          描述:
                        </div>
                        <Form.Item noStyle name="description">
                          <Input className="w-2/3" />
                        </Form.Item>
                      </Col>
                      <Col span={24} className="my-3">
                        <div className="inline-block" css={{ width: 60 }}>
                          創作者:
                        </div>
                        <Form.Item
                          noStyle
                          name="author"
                          rules={[rules.required]}>
                          <Input className="w-2/3" />
                        </Form.Item>
                      </Col>
                    </Row>
                  </Collapse.Panel>
                  <Collapse.Panel header="工具列" key={2}>
                    <Row>
                      <Col span={24}>
                        <SnippetListMenu />
                      </Col>
                    </Row>
                  </Collapse.Panel>
                </Collapse>
              </Col>

              <Col
                span={19}
                className="report-template-builder__main-content rounded-tl-lg">
                <SnippetsContainer
                  draggable={!isPreview}
                  snippetList={snippetList}
                  onSnippetUpdate={handleSnippetUpdate}
                />
              </Col>
            </Row>
          </Form>
        </Page>
      </Spin>
    </ReactDndProvider>
  )
}
