import React from 'react'
import { ModalProps, ModalFuncProps } from 'antd/lib/modal'
import { find, path, pathOr } from 'ramda'
import Modal from '@/components/modal'
import { modalDefaultProps } from '@/utils/message'
const { compose } = require('ramda')

export const confirm = Modal.confirm

type Props<T> = {
  /**
   * 確認視窗的文字內容
   */
  content?: string | React.ReactNode
  /**
   * 確認後執行的行為，例如打 API
   */
  onOk?: (values?: any, resolve?: (values?: any) => any) => any
  /**
   * 跳出確認時可以做的事情，例如關掉正在編輯的對話框
   */
  onBeforeOpen?: () => any
  /**
   * 成功後，可重新取得(列表)資料
   */
  refetchQueries?: () => any
  /**
   * 取消後做的事情，例如重新打開編輯對話窗
   */
  onCancel?: () => any
  /**
   * 列表資料，可在跳確認窗時從裡面查找要確認修改的資料並回傳
   */
  dataSource?: T[]
  /**
   * 項目，可以是單一物件或陣列物件...
   */
  item?: any
  /**
   * 自訂確認視窗屬性
   */
  modalProps?: ModalProps
  /**
   * 成功後可作的事情
   */
  onCompleted?: () => any
}

export let modal: {
  destroy: () => void
  update: (newConfig: ModalFuncProps) => void
}

export function useConfirm<T extends any>(props: Props<T>) {
  const {
    content,
    dataSource = [],
    onOk,
    onBeforeOpen,
    refetchQueries,
    onCancel,
    item,
    modalProps = {},
    onCompleted,
  } = props

  let onCompletedResolve = React.useRef(() => ({}))

  const handleToggleConfirm = (evt: React.MouseEvent<HTMLElement>) => {
    if (onBeforeOpen) {
      onBeforeOpen()
    }
    handleConfirm(evt)
  }

  const handleFailure = () => {
    if (modal) {
      modal.update({
        okButtonProps: {
          className: 'c-btn c-btn--danger',
          style: { minWidth: 96, marginRight: 16 },
          loading: false,
        },
      })
    }
  }

  const handleConfirm = (evt: React.MouseEvent<HTMLElement>) => {
    const _item = compose(
      pathOr(item, []),
      find<any>(
        x =>
          `${path(['id'], x)}` ===
          (evt.currentTarget as HTMLElement).dataset['id']
      )
    )(dataSource)

    modal = confirm({
      ...modalDefaultProps(content, modalProps),
      okText: '確認',
      onOk() {
        if (modal) {
          modal.update({
            okButtonProps: {
              className: 'c-btn c-btn--danger',
              style: { minWidth: 80, marginRight: 24 },
              loading: true,
            },
          })
        }

        return new Promise(resolve => {
          if (onOk) {
            onOk(_item)
          }

          if (onCompleted) {
            onCompleted()
          }

          onCompletedResolve.current = (() => {
            resolve()
            if (refetchQueries) {
              refetchQueries()
            }
          }) as any
        })
      },
      onCancel() {
        if (onCancel) {
          onCancel()
        }
      },
    })
  }

  return [handleToggleConfirm, handleFailure] as [
    (evt: React.MouseEvent<HTMLElement>) => void,
    () => void
  ]
}

export default useConfirm
